/* CONTAINERS */
body {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 0px;
  margin: 0px;
  padding: 5px;
  font-size: 12px;
  cursor: default;
  width: '100%';
  height: '100%';
}

.default {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  margin: 5px;
  padding: 5px;
  font-size: 12px;
  cursor: default;
}

/* POPUPS (MENUS E MODAIS) */

.popup-bottom {
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 0;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  margin: 0;
  z-index: 1;
}

.popup-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #ffffff;
  align-items: center;
  border-radius: 5px;
  padding: 20px;
  cursor: default;
  z-index: 2;
}

/* T√çTULOS */
.title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  margin: 5px;
  padding: 5px;
  color: rgba(32, 122, 140, 0.8);
  font-weight: bold;
  font-size: 14px;
  cursor: default;
}

.p {
  margin: 0px;
  padding: 5px;
}

/* INPUTS E TEXTAREAS */
.input {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #1f7a8c;
  font-weight: bold;
  background-color: #e1e5f2;
  border: 0px;
  border-color: transparent;
  border-radius: 5px;
  outline: 0px;
  box-shadow: transparent;
  margin: 5px;
  height: 50px;
  cursor: default;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.textarea {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 85px;
  resize: none;
  padding: 10px;
  text-align: left;
  text-transform: uppercase;
  color: #1f7a8c;
  font-weight: bold;
  background-color: #e1e5f2;
  border-width: 0px;
  border-color: #e1e5f2;
  border-radius: 5px;
  outline: 0px;
  box-shadow: transparent;
  margin: 5px;
  cursor: default;
}

.textarea::-webkit-scrollbar {
  width: 20px;
}

.textarea::-webkit-scrollbar-track {
  background-color: #bfdbf7;
  border-radius: 5px;
}

.textarea::-webkit-scrollbar-button {
  display: none;
}

.textarea::-webkit-scrollbar-corner {
  display: none;
}

.textarea::-webkit-scrollbar-thumb {
  background-color: #1f7a8c;
  border-radius: 5px;
}

.textarea::-webkit-scrollbar-thumb:hover {
  background-color: #279ab1;
  border-radius: 5px;
}

