/* LISTAS */
.row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  background-color: transparent;
  border: 0px;
  border-color: transparent;
  border-radius: 5px;
  outline: 0px;
  box-shadow: transparent;
  padding: 0px;
  padding-right: 5px;
  margin: 0px;
  margin-bottom: -5px;
  opacity: 0.8;
  cursor: default;
}

.row:hover {
  opacity: 1;
}

.widget {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  background-color: rgb(31, 122, 140, 0.5);
  border: 0px;
  border-color: transparent;
  border-radius: 5px;
  outline: 0px;
  box-shadow: transparent;
  min-height: 50px;
  margin: 5px;
  padding: 10px;
  text-align: center;
  font-weight: bold;
  cursor: default;
}

/* ANIMA√á√ïES */
.fade-in {
  animation-name: fadein;
  animation-duration: 0.5s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.fade-out {
  animation-name: fadeout;
  animation-duration: 0.5s;
  animation-delay: 2s;
}

.toast {
  animation: fadeinout 3s ease-in-out forwards;
}

@keyframes fadeinout {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

/* RESPONSIVIDADE PARA CELULARES */
@media only screen and (max-width: 600px) {
  .main {
    font-size: 10px;
  }
  .linegroup {
    flex-direction: column;
  }
  .menucontainer {
    padding: 10px;
    background-color: #ebedef;
  }
}

/* RESPONSIVIDADE PARA TABLETS*/
@media only screen and (max-width: 800px) {
  .main {
    font-size: 12px;
  }
}